/* Fullscreen Reset */
html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  min-height: 100vh; /* ✅ let page grow */
  overflow-x: hidden;
  overflow-y: auto; /* ✅ critical to enable scroll */
  background-color: black;
}

/* Hero Wrapper should not restrict height */
.heroWrapper {
  position: relative;
  width: 100%;
  /* Remove height: 100vh */
  overflow: visible;
}

/* Hero Carousel */
.heroCarousel {
  position: relative;
  z-index: 10;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

/* Background Carousel */
.bgCarousel {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

/* Video Container */
.videoContainer {
  position: fixed;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 1s ease;
  top: 0;
  left: 0;
}

.videoContainer.active {
  opacity: 1;
}

.video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
}

/* Foreground Carousel */
.fgCarousel {
  position: absolute;
  top: 50%;
  left: 10%;
  transform: translateY(-50%);
  width: 200px;
  height: 300px;
  z-index: 5;
}

.fgVideoContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 1s ease;
}

.fgVideoContainer.active {
  opacity: 1;
}

.fgVideo {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Text */
.textContent {
  position: absolute;
  bottom: 15%;
  left: 10%;
  z-index: 10;
  color: white;
}

.title {
  font-size: 2.5rem;
  font-weight: 300;
  letter-spacing: 0.05em;
  margin-bottom: 2rem;
  line-height: 1.2;
}

.cta {
  display: flex;
  gap: 2rem;
}

.caption {
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.2em;
}

/* Controls */
.rightControls {
  position: absolute;
  right: 2rem;
  bottom: 20%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 2rem;
  z-index: 10;
}

/* Nav buttons omitted for brevity — keep yours */

/* Overlay Section */
.overlaySection {
  position: relative;
  z-index: 20;
  width: 70vw;
  height: 60vh;
  background: white;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  transform-origin: center;
  border-radius: 24px;
  overflow: hidden;
  padding: 2rem;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.3);
  margin: 0 auto;
  top: -20vh;
  padding: 3rem 2rem 1rem 2rem;
  align-items: flex-start; 
}

/* Extra space for scroll */

.categoryGrid {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 1rem;
  width: 80%;
}

.categoryCard {
  flex: 1 1 18%;
  min-width: 170px;
  max-width: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.categoryImage {
  width: 100%;
  aspect-ratio: 3 / 5;
  background-size: cover;
  background-position: center;
  border-radius: 12px;
  transform: scale(1.1); /* slightly larger by default */
  transition: transform 0.4s ease;
}
.categoryImage:hover {
  transform: scale(1.15);
}

.categoryLabel {
  margin-top: 0.75rem;
  font-size: 0.75rem;
  color: black;
  font-weight: 500;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
